












import { computed, defineComponent } from '@vue/composition-api';
import { isNullOrEmpty } from '@/app/shared/client/helper/base';
import useFocusHandler from '@/app/editor/client/logic/focus';
import useI18N from '@/app/shared/client/logic/i18n';
import { MultilineTextEditorControl } from '@/app/editor/client/view/component/control';

const MultilineTextContentEditor = defineComponent({
  components: {
    MultilineTextEditorControl,
  },
  props: {
    label: {
      required: true,
      type: String,
    },
    placeholderLabel: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: String,
    },
  },
  setup(props, { root, emit }) {
    const i18n = useI18N(root.$store);

    const localValue = computed({
      get: () => props.value,
      set: (v) => emit('editor-action', v),
    });
    const { isFocused, onFocus, onBlur } = useFocusHandler();

    return {
      i18n,
      localValue,
      isEmpty: computed(() => isNullOrEmpty(props.value)),
      isFocused,
      onFocus,
      onBlur,
    };
  },
});

export default MultilineTextContentEditor;
